/******************************************************/
/* pc category-nav
/******************************************************/
#category-nav{
  width: 260px;
  height: 100%;
  position: fixed;
  left:-260px;
  top:0;
  background: #444;
  box-sizing: border-box;
  color: #fff;
  transition: all .5s ease;
  z-index: 20;
  a{
      color: #fff;
      text-decoration: none;
      font-size: 12px;
  }

  a:visited{
      color: #fff;
      text-decoration: none;
  }

  a:hover{
      color: #ccc;
      text-decoration: none;
  }

  a:active{
      color: #fff;
      text-decoration: none;
  }
  .scroller{
    padding:60px 30px;
  }
  .category-list{
    margin-bottom: 30px;
  }  
}
#overray{
  position: fixed;
  width: 100%;
  height: 100%;
  left:0;
  top: 0;
  background: rgba(0,0,0,0.5);
  z-index: 10;
  display: none;
}

#contents-slide-wrap.active #category-nav{
  left:0px;
}


/******************************************************/
/* pagetop
/******************************************************/

.pagetop{
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: -40px;
  right: 20px;
  z-index: 20;
  border-radius: 20px;
  background: #444;
  cursor: pointer;
  transition: all 0.3s ease;
  &:after{
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    margin-top: 5px;
    border-top: 2px solid #fff;
    border-right: 2px solid #fff;
    transform: rotate(-45deg);
  }
  &:hover{
    background: #666;
  }
}

.pagetop.active{
  bottom: 20px;
}
